import {
  Directive, ElementRef, EventEmitter, HostListener, Input, Output, Renderer2
} from '@angular/core';
import {PaymentOptions} from './payment-models';

@Directive({
  selector: '[appPaymentRequest]'
})
export class PaymentRequestDirective {
  @Input('options') options: PaymentOptions = {};
  @Input('items') items: PaymentItem[];
  @Input('shippingOptions') shippingOptions : any[];
  @Output() onCheckout = new EventEmitter();
  @Output() onShippingOptionChange = new EventEmitter();
  payment: PaymentRequest;
  constructor(private el: ElementRef, private renderer: Renderer2) {
   if(this.isPaymentSupported()) {
     this.items = [];
     this.options = Object.assign({}, this.options, {
       supportedPaymentMethods: [{supportedMethods: 'basic-card'}],
       paymentDetails: {
         total: {
           label: 'Total',
           amount: {
             currency: 'USD',
             value: 0
           }
         },
         shippingOptions: this.shippingOptions,
         displayItems: this.items
       },
       payerOptions: {}
     });

     this.updatePayment();
   }  else {
     this.renderer.setAttribute(this.el.nativeElement, 'disabled', 'true');
   }

  }

  /**
   * Update native payment request object.
   */
  updatePayment(): void {
    this.options.paymentDetails.displayItems = this.items;
    let total = 0;
    this.items.forEach((item)=> {
      total =+ parseFloat(<string>item.amount.value);
    });
    this.options.paymentDetails.total.amount.value = <any>total;
    this.payment = new PaymentRequest(this.options.supportedPaymentMethods, this.options.paymentDetails, this.options.payerOptions);
    this.handleShippingChangeEvent();
  }

  /**
   * Trigger Payment request native popup
   */
  checkout(): void {
    this.updatePayment();
    this.payment.show().then((paymentResponse: PaymentResponse)=> {
      paymentResponse.complete();
      this.onCheckout.emit(paymentResponse);
    }).catch((err)=> {
      this.onCheckout.emit({
        error: err,
        success: false
      });
    })

  }

  /**
   * Handle shipping option change event.
   */
  handleShippingChangeEvent(): void {
    this.payment.addEventListener('shippingoptionchange', (event)=> {
    const paymentRequest = event.target;
    const selectedId = (<any>paymentRequest).shippingOption;
    let res: {shippingOption?: any, total?: any} = {};
    this.options.paymentDetails.shippingOptions.forEach((shippingOption)=> {
      if(shippingOption.id === selectedId) {
        shippingOption.selected = true;
        this.options.paymentDetails.total.amount.value = <any>(parseFloat(this.options.paymentDetails.total.amount.value) + parseFloat(shippingOption.amount.value));
        res.shippingOption = shippingOption;
        res.total = this.options.paymentDetails.total.amount.value;
      }
    });
      (<any>event).updateWith(new Promise((resolve)=> {
      //return updated total value and selected shipping option.
      this.onShippingOptionChange.emit(res);
      resolve(this.options.paymentDetails);
    }));
    });
  }

  @HostListener('click', ['$event']) onClick($event){
    if(this.isPaymentSupported) {
      this.checkout();
    }
  }

  isPaymentSupported(): boolean {
    return !!(<any>window).PaymentRequest;
  }

}
